@import '../../../styles/libs';

.tooltip-container {
  display: flex;
  align-items: center;
  width: fit-content;
  height: fit-content;

  > .tooltip-opener {
    cursor: help;
    width: fit-content;
    height: fit-content;
  }

  > .tooltip-message-container {
    position: relative;
    z-index: 10;
    height: 0;
    display: flex;
    align-items: center;
    @include fade-in(0.3s);
    &.closing {
      @include fade-out(0.3s);
    }

    > .triangle {
      border: transparent;
      position: absolute;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 0 7.5px;
      z-index: 1;
    }

    > .tooltip-message-wrap {
      position: absolute;
      color: $WHITE;
      width: fit-content;
      z-index: 2;
      background-color: $PURPLE-600;
      width: fit-content;
      white-space: pre;
      font-size: $SMALL3X;
      padding: 8px 10px;
      border-radius: 8px;
      margin: 4px 0;
    }
  }

  &.float-bottom {
    flex-direction: column;
    > .tooltip-message-container {
      flex-direction: column;
      > .triangle {
        top: 0px;
        border-bottom-color: $PURPLE-600;
        border-bottom-width: 5px;
      }

      > .tooltip-message-wrap {
        top: 0;
      }
    }
  }

  &.float-top {
    flex-direction: column-reverse;
    > .tooltip-message-container {
      flex-direction: column-reverse;
      > .triangle {
        bottom: 0px;
        border-top-color: $PURPLE-600;
        border-top-width: 5px;
      }
    }
  }
}
